import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

//    
const Wrap = styled.footer`
  margin-top: 48px;
  background: var(--surface-footer-bg); /* <--  */
  border-top: 1px solid var(--border-primary); /* <--  */
  padding: 40px 0;
`;

const Grid = styled.div`
  display: grid;
  gap: 24px;
  grid-template-columns: 1.5fr 1fr 1fr 1fr;
  @media(max-width: 820px) {
    grid-template-columns: 1fr 1fr;
  }
  @media(max-width: 540px) {
    grid-template-columns: 1fr;
    text-align: center;
  }
`;

const FooterColumn = styled.div`
  display: flex;
  flex-direction: column;
  gap: 12px;
`;

const FooterLink = styled(Link)`
  color: var(--text-secondary); /* <--  */
  opacity: 1; /* <--  */
  text-decoration: none;
  font-size: 12px;
  transition: all 0.2s ease;
  &:hover {
    opacity: 1;
    color: var(--accent-turquoise);
    text-shadow: 0 0 8px var(--accent-turquoise);
    transform: translateX(3px);
  }
  @media(max-width: 540px) {
    &:hover {
      transform: translateX(0) scale(1.05);
    }
  }
`;
//     

const Logo = styled.img`
  width: 200px;
  height: 100px;
  image-rendering: pixelated;
  @media(max-width: 540px) {
    margin: 0 auto;
  }
`;

export default function Footer() {
  return (
    <Wrap className="footer">
      <div className="container">
        <Grid>
          {/*  1:    */}
          <FooterColumn>
            {/* ---   --- */}
            <Logo src={process.env.PUBLIC_URL + '/assets/levelup.png'} alt="LevelUp Logo" />
            
            <p className="p">BiTZone     . ,       !</p> {/* [cite: 47] */}
          </FooterColumn>

          {/*  2:  */}
          <FooterColumn>
            <h3 className="h2 mono" style={{ color: 'var(--accent-yellow)' }}></h3> {/* <--  */}
            <FooterLink to="/trade-in"> (Trade-In)</FooterLink>
            <FooterLink to="/buyback">   </FooterLink>
            <FooterLink to="/installment"></FooterLink>
            <FooterLink to="/dropshipping"></FooterLink>
          </FooterColumn>

          {/*  3:  */}
          <FooterColumn>
            <h3 className="h2 mono" style={{ color: 'var(--accent-turquoise)' }}></h3> {/* <--  */}
            <FooterLink to="/about">  </FooterLink>
            <FooterLink to="/delivery">  </FooterLink>
            <FooterLink to="/returns">  </FooterLink>
            <FooterLink to="/contacts">  </FooterLink>
          </FooterColumn>

          {/*  4:   */}
          <FooterColumn style={{ alignItems: 'center', gap: 8 }}>
            <h3 className="h2 mono" style={{ color: 'var(--accent-pink)' }}>  </h3> {/* <--  */}
     
            {/* ---    --- */}
            <img src={process.env.PUBLIC_URL + '/assets/qr-code.png'} alt="QR code for Telegram" style={{ width: 120, height: 120, maxWidth: '100%' }} />
            <p className='p' style={{fontSize: 10, textAlign: 'center'}}>  '!</p>
          </FooterColumn>
        </Grid>
        <div style={{ marginTop: 32, opacity: .7, fontSize: 12, textAlign: 'center', color: 'var(--text-secondary)' }}> {/* <--  */}
    
           {new Date().getFullYear()} BiTZone by kysildev.   .
        </div>
      </div>
    </Wrap>
  );
}